<template>
  <div class="search-result">
    <van-list
      v-model="loading"
      :finished="finished"
      finished-text="没有更多了"
      @load="onLoad"
    >
      <van-cell v-for="item in list" :key="item.art_id" :title="item.title" />
    </van-list>
  </div>
</template>

<script>
import { getSearchResultAPI } from '../../../api/index.js'

export default {
  name: 'searchResult',

  props: {
    searchText: {
      type: String,
      default: ''
    }
  },

  data () {
    return {
      page: 1, // 当前页
      perPage: 20, // 每页数量
      list: [],
      loading: false,
      finished: false,
      err: false
    }
  },

  methods: {
    async onLoad () {
      // 异步更新数据
      // 第一步请求获取更新数据
      try {
        const { data: res } = await getSearchResultAPI({
          page: this.page,
          per_page: this.perPage,
          q: this.searchText
        })
        this.list.push(...res.data.results)
        this.loading = false

        if (res.data.results.length) {
          // 更新一下页码
          this.page++
        } else {
          // 列表没有数据.那么finished改为true 不再调用onload
          this.finished = true
        }
      } catch (err) {
        this.err = true
        this.loading = false
        this.$toast('请求失败, 请重试')
      }
    }
  }
}
</script>

