<template>
  <div class="search-container">
    <!-- 顶部搜索 -->
    <!-- 移动端键盘显示搜索按钮需要用form标签包裹 -->
    <form action="/">
      <van-search
        v-model="searchText"
        background="#3296fa"
        placeholder="请输入搜索关键词"
        show-action
        @search="onSearch"
        @cancel="onCancel"
        @focus="isResultShow = false"
      />
    </form>

    <!-- 搜索结果 -->
    <searchResult v-if="isResultShow" :searchText="searchText"></searchResult>

    <!-- 联想搜索列表 -->
    <searchSuggestion
      v-else-if="searchText"
      :searchText="searchText"
      @search="onSearch"
    >
    </searchSuggestion>

    <!-- 搜索历史 -->
    <searchHistory v-else></searchHistory>

  </div>
</template>

<script>
/*
  01 进入搜索页面时显示的是 搜索历史页 ==> searchHistory
  02 输入内容后显示的是 联想列表 ==> searchSuggestion
  03 清空输入内容时(searchText: '') 搜索历史页 ==> searchHistory
  04 触发@search时间 搜索结果页 ==> searchResult
  05 重新聚焦 联想列表 ==> searchSuggestion (@focus="isResultShow = false" 关闭 搜索结果页)
*/
import searchHistory from './components/searchHistory.vue'
import searchSuggestion from './components/searchSuggestion.vue'
import searchResult from './components/searchResult.vue'

export default {
  name: 'searchIndex',

  components: {
    searchHistory,
    searchSuggestion,
    searchResult
  },

  data () {
    return {
      searchText: '', // 搜索文本框内容
      isResultShow: false // 是否展示搜索结果
    }
  },

  methods: {
    // 搜索事件
    onSearch (value) {
      // 将当前的值赋值给输入框
      this.searchText = value
      // 主动开启搜索结果组件
      this.isResultShow = true
      console.log(value)
    },

    onCancel () {
      // 点击取消时返回前一个路由页面
      this.$router.back()
    }
  }
}
</script>

<style lang="less" scoped>
  .search-container {
    .van-search__action {
      color: #fff;

      &:active {
        background-color: #3296fa;
      }
    }

    // 将搜索栏置顶， 并设置padding-top来限制文本区域的滚动范围
    .van-search {
      position: fixed;
      top: 0;
      left: 0;
      right: 0;
      z-index: 1;
    }
  }
</style>
