<template>
  <div>
    <van-cell
      v-for="(item, index) in suggestionList"
      :key="index"
      :title="item"
      icon="search"
      @click="$emit('search', item)"
    >
      <template #title>
        <span v-html="hightLight(item)"></span>
      </template>
    </van-cell>
  </div>
</template>

<script>
import { getSearchsuggestionAPI } from '../../../api/index.js'
// 引入防抖方法
import { debounce } from 'lodash'

// 手写防抖
/*

  function myDebounce (calback, times) {
    // 通过闭包延迟时间timer的生命周期
    let timer = null
    return function (val) {
      const that = this
      if (timer) {
        clearTimeout(timer)
      }
      timer = setTimeout(() => {
        calback.call(that, val)
      }, times)
    }
  }
  myDebounce(业务函数, 节流时间(毫秒))
*/

export default {
  name: 'searchSuggestion',

  props: {
    searchText: {
      type: String,
      default: ''
    }
  },

  data () {
    return {
      suggestionList: []
    }
  },

  watch: {
    searchText: {
      // lodash的防抖函数必须使用普通函数 debounce(function(){}, 连续触发时间)
      handler: debounce(function (newValue) {
        this.loadSearchSuggestion(newValue)
      }, 200),
      immediate: true //  首次监听触发
    }
  },

  methods: {
    async loadSearchSuggestion (newVal) {
      try {
        const { data: res } = await getSearchsuggestionAPI(newVal)
        console.log(res)

        // 调用接口发现, 有些情况下后端会返回一个 [null] ，针对这种情况, 我们可以主动的将这个null改为当前searchText的值
        if (res.data.options && res.data.options[0] == null) {
          res.data.options[0] = this.searchText
        }

        // 实现文本高亮结构处理 => <span style="color: red">后</span>
        // 方法一
        /*
          const list = res.data.options.map(item => {
          const highlightStr = `<span style="color: red">${newVal}</span>`

          // 通过正则找到目标字符
          const reg = new RegExp(newVal, 'gi')

          使用replace() 方法替换
          return item.replace(reg, highlightStr)
          })
        */

        // 方法二: split 与 join 结合使用
        // const list = res.data.options.map(item => {
        //   // 01 设置关键字的高亮文本样式
        //   const highlightStr = `<span style="color: red">${newVal}</span>`
        //   // 02 通过 split 按照目标字符串拆分成数组
        //   const arr = item.split(newVal)
        //   // 03 通过 join 方法, 将highlightStr 拼接到每个元素之间
        //   return arr.join(highlightStr)
        // })
        // this.suggestionList = list
        this.suggestionList = res.data.options
      } catch (err) {
        this.$toast('获取搜索失败')
      }
    },

    // 提取高亮文本方法
    hightLight (item) {
      const highlightStr = `<span style="color: red">${this.searchText}</span>`
      const arr = item.split(this.searchText)
      // 03 通过 join 方法, 将highlightStr 拼接到每个元素之间
      return arr.join(highlightStr)
    }
  }
}
</script>

